@import 'setting';
@import 'commons';
@import 'containers/header';
@import 'containers/home';
@import 'containers/footer';
@import 'containers/detail';

.main-content {
    width: 100%;
    box-sizing: border-box;
    float: left;
    .item-container {
        padding: 20px;
    }
    .container {
        background: #ffffff;
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        min-height: 600px;
        margin-top: -30px;
        margin-bottom: -30px;
        position: relative;
        display: flex;
    }
    .fix-toolbar {
        display: 'flex';
        align-items: 'center';
        justify-content: 'flex-end';
        padding: '0 8px';
    }
    .card-title {
        color: #333;
        font-size: 20px;
        margin: 0;
    }
    .list-box {
        padding: 20px 0;
        display: flex;
        float: left;
        &.category {
            padding: 30px;
        }
        .box-header {
            border-bottom: 2px solid #dfdfdf;
            margin-bottom: 15px;
            float: left;
            width: 100%;
            box-sizing: border-box;
            h2 {
                font-weight: normal;
                font-size: 16px;
                line-height: normal;
                text-transform: uppercase;
                background: #fff;
                padding-right: 15px;
                color: #797979;
                float: left;
            }
            .view-more {
                float: right;
                padding-top: 5px;
                a {
                    font-weight: normal;
                    font-size: 12px;
                    line-height: normal;
                    text-decoration-line: underline;
                    color: #30b967;

                    &:visited {
                        color: #30b967;
                    }
                }
            }
        }
        .list-wrp {
            padding: 0 20px;
        }
        .post-item {
            .item-img {
                width: 100%;
                height: 120px;
                overflow: hidden;
                box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1);
                img {
                    width: 100%;
                }
            }
            .item-info {
                display: flex;
                flex-direction: column;
                .title {
                    padding: 10px 0;
                    a {
                        text-decoration: none;
                        h3 {
                            font-size: 14px;
                            line-height: normal;
                            color: #000000;
                            height: 34px;
                            overflow: hidden;
                        }
                    }
                }
            }
        }
    }
    .price {
        font-weight: bold;
        font-size: 14px;
        line-height: normal;
        color: #30b967;
        .currency {
            color: #c4c4c4;
            font-weight: normal;
            padding-left: 5px;
        }
    }
    .search-result-info-wrp {
        width: 100%;
        float: left;
        padding-bottom: 20px;
        padding-top: 20px;
        border-bottom: 1px solid #ccc;
        margin-bottom: 20px;
        color: #444;
    }
    .search-result-info {
        font-size: 16px;
        float: left;
    }
    .search-result-action {
        float: right;
    }
    .loading-wrp {
        padding: 30px;
        padding-top: 100px;
        box-sizing: border-box;
    }
    .paging {
        width: 100%;
        display: flex;
        margin-top: 30px;
        justify-content: center;
        padding: 30px 0;
        button {
            padding-right: 40px;
            padding-left: 40px;
            text-transform: uppercase;
        }
    }
    .right-box {
        .latest-post,
        .other-post {
            width: 100%;
            max-width: 100%;
            flex-basis: 100%;
            align-items: flex-start;
        }
    }
}
.main-ct-right {
    padding-left: 20px;
    box-sizing: border-box;
}
.right-box {
    padding-bottom: 50px;
    h3 {
        font-size: 16px;
        text-transform: uppercase;
        padding-bottom: 20px;
        width: 100%;
        color: #777;
    }
    .post-item {
        float: left;
        width: 100%;
        padding-bottom: 20px;
        a {
            text-decoration: none;
        }
        .img {
            width: 40%;
            float: left;
            height: 70px;
            overflow: hidden;
            img {
                width: 100%;
            }
        }
        .card-title {
            float: left;
            padding-left: 10px;
            box-sizing: border-box;
            width: 60%;
            font-weight: normal;
            font-size: 13px;
            line-height: normal;
            color: #000000;
            max-height: 46px;
            overflow: hidden;
        }
        .price {
            padding-left: 10px;
            float: left;
            width: 60%;
            box-sizing: border-box;
        }
    }
}
.other-post {
    margin-top: 15px;
}
.container {
    width: $container;
    margin: 0 auto;
}
.container-left {
    width: $leftWidth;
}
.main-ct-left {
    float: left;
}
.main-ct-right {
    float: left;
}
.container-right {
    width: $rightWidth;
}
.page {
    .page-wrp {
        padding: 30px;
    }
}

@import 'responsive';
