$green: #2ecc71;
$darkGreen: #4aa05a;
$blue: #55acee;
$darkBlue: rgb(63, 137, 185);
$dark: #252525;
$softDark: #4d4c4c;
$thinShadow: 0 1px 1px rgba(0, 0, 0, 0.3);
$normalShadow: 0 2px 2px rgba(0, 0, 0, 0.3);
$transition: all 0.3s ease-in;
$red: #e74c3c;
$orange: #f99a0c;
$yellow: #f1c40f;
$purple: #9b59b6;
$container: 1100px;
$leftWidth: 800px;
$rightWidth: 240px;

$default-background-color: #fafafa;
$default-text-color: #222;

$primary-background-color: $darkBlue;
$primary-text-color: #fafafa;

$info-background-color: $blue;
$info-text-color: #fafafa;

$success-background-color: $green;
$success-text-color: #fafafa;

$warning-background-color: $orange;
$warning-text-color: #fafafa;

$danger-background-color: $red;
$danger-text-color: #fafafa;

$inverse-background-color: $dark;
$inverse-text-color: #fafafa;

$purple-background-color: $purple;
$purple-text-color: #fafafa;

// Cross-browser helpers

@mixin boxShadow($statement) {
    -webkit-box-shadow: $statement;
    -moz-box-shadow: $statement;
    box-shadow: $statement;
}
@mixin transition($property, $duration, $transition: linear) {
    -webkit-transition-property: $property;
    -webkit-transition-duration: $duration;
    -webkit-transition-timing-function: $transition;
    -moz-transition-property: $property;
    -moz-transition-duration: $duration;
    -moz-transition-timing-function: $transition;
    -ms-transition-property: $property;
    -ms-transition-duration: $duration;
    -ms-transition-timing-function: $transition;
    -o-transition-property: $property;
    -o-transition-duration: $duration;
    -o-transition-timing-function: $transition;
    transition-property: $property;
    transition-duration: $duration;
    transition-timing-function: $transition;
}

@mixin buttonColor($background, $text) {
    background: $background;
    color: $text;

    &:hover,
    &:focus {
        background: darken($background, 8%);
        color: $text;
    }
}
@mixin buttonSize($height, $depth, $font-size) {
    height: $height;
    padding: 0 $font-size - 4;
    border-radius: $depth + 2;
    @include boxShadow(inset 0 -$depth 0 0 rgba(0, 0, 0, 0.15));
    font-size: $font-size;
    line-height: 0;
    i {
        line-height: 0;
        &.left {
            margin-right: 5px;
        }
        &.right {
            margin-left: 5px;
        }
    }
    &:active,
    &:active i {
        line-height: $height;
    }
    &.btn-primary,
    &.btn-info,
    &.btn-success,
    &.btn-warning,
    &.btn-danger,
    &.btn-purple,
    &.btn-inverse {
        @include boxShadow(inset 0 0 -$depth 0 0 rgba(0, 0, 0, 0.2));
    }
}
.hth-project {
    .btn {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        cursor: pointer;
        border: none;
        @include buttonSize(40px, 3px, 18px);
        @include transition(background-color, 0.2s);
        @include buttonColor($default-background-color, $default-text-color);
        &.btn-primary {
            @include buttonColor(
                $primary-background-color,
                $primary-text-color
            );
        }
        &.btn-info {
            @include buttonColor($info-background-color, $info-text-color);
        }
        &.btn-success {
            @include buttonColor(
                $success-background-color,
                $success-text-color
            );
        }
        &.btn-warning {
            @include buttonColor(
                $warning-background-color,
                $warning-text-color
            );
        }
        &.btn-danger {
            @include buttonColor($danger-background-color, $danger-text-color);
        }
        &.btn-inverse {
            @include buttonColor(
                $inverse-background-color,
                $inverse-text-color
            );
        }
        span {
            pointer-events: none;
        }

        &:hover {
            text-decoration: none;
        }
        &:focus {
            text-decoration: none;
            outline: none;
        }
        &:active {
            @include boxShadow(#{'none !important'});
        }
    }
    .btn-small {
        @include buttonSize(30px, 2px, 14px);
    }
    .btn-large {
        @include buttonSize(50px, 4px, 22px);
    }
    .btn-xlarge {
        @include buttonSize(60px, 5px, 26px);
    }
    .btn-inline {
        display: inline-block;
        vertical-align: middle;
    }
    .bold {
        font-weight: bold;
    }

    .blue {
        background-color: $blue;
        color: #fff;
    }
    .red {
        background-color: $red;
        color: #fff;
    }
    .pink {
        background-color: palevioletred;
        color: #fff;
    }
    .purple {
        background-color: $purple;
        color: #fff;
    }
    .soft-dark {
        background-color: $softDark;
        color: #fff;
    }
    .blue-dark {
        background-color: $darkBlue;
        color: #fff;
    }
    .green {
        background-color: $green;
        color: #fff;
    }
    .green-dark {
        background-color: $darkGreen;
        color: #fff;
    }
    .orange {
        background-color: $orange;
        color: #fff;
    }
    .yellow {
        background-color: $yellow;
        color: #fff;
    }
    .dark {
        background-color: $dark;
        color: #fff;
    }
    .color-red {
        color: $red !important;
    }
    .color-blue {
        color: $blue !important;
    }
    .color-green {
        color: $green !important;
    }
    .color-orange {
        color: $orange !important;
    }
    .color-yellow {
        color: $yellow !important;
    }
    .color-dark {
        color: $dark !important;
    }
    .color-purple {
        color: $purple !important;
    }
    .color-darkBlue {
        color: $darkBlue !important;
    }
    .color-darkGreen {
        color: $darkGreen !important;
    }
    .component-loading {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 20px;
        .loading-text {
            padding-left: 10px;
        }
    }
    .blink-text {
        animation: blinker 1s linear infinite;
    }
    @keyframes blinker {
        50% {
            opacity: 0;
        }
    }
    .show {
        display: block !important;
    }
    .hide {
        display: none !important;
    }
}
