.detail {
    padding: 20px;
    .top-detail {
        width: 100%;
        float: left;
        padding-bottom: 10px;
        border-bottom: 1px dotted #ccc;
        margin-bottom: 20px;
        .product-info-w {
            float: left;
            width: 40%;
            padding-left: 15px;
            box-sizing: border-box;
            font-size: 16px !important;
            line-height: 22px !important;
            span {
                font-size: 16px !important;
                line-height: 28px !important;
            }
            .price {
                font-size: 22px !important;
                font-weight: 600;
            }
            .currency {
                padding-left: 5px;
                font-size: 16px !important;
                color: #777;
            }
        }
        .slider {
            width: 60%;
            float: left;
            .carousel {
                .carousel__image {
                    &.carousel__zoom-image {
                        background-size: 100% !important;
                        background-repeat: no-repeat;
                    }
                }
                .thumbs {
                    float: left;
                    width: 100%;
                    margin: 10px 0;
                    padding-left: 10px;
                    button {
                        width: 100px;
                        background: transparent;
                        margin-right: 10px;
                        padding: 0;
                        border: none;
                        height: 70px;
                        overflow: hidden;
                        float: left;
                        margin-bottom: 10px;
                        img {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
    .product-info {
        display: none;
    }
    .detail-header {
        h1 {
            font-size: 28px;
            padding: 0 0 20px 0;
            font-weight: bold;
            line-height: 22px;
        }
    }
    .detail-content {
        line-height: 22px;
        background: #fff;
        //box-shadow: $thinShadow;
        margin-bottom: 30px;
        width: 100%;
        box-sizing: border-box;
        strong,
        b {
            font-weight: bold;
        }
        img {
            margin: 15px 0;
            max-width: 100%;
            height: auto;
        }
    }
    .comment-box {
        width: 100%;
        background: #f9f9f9;
        padding: 15px;
        box-sizing: border-box;
        box-shadow: $thinShadow;
        margin-bottom: 30px;
    }
    .detail-wrp {
        align-items: flex-start;
    }
}
