/* For mobile phones: */
[class*='col-'] {
    width: 100%;
}

@media only screen and (max-width: 600px) {
    .main-container {
        .header {
            .header-wrp {
                .toolbar {
                    padding: 0;
                    min-height: 50px;
                }
                .logo {
                    img {
                        width: 100px;
                    }
                }
            }
        }
        .main-content {
            padding: 30px 10px;
            .item-container {
                .search-result-info {
                    font-size: 15px;
                }
                padding: 10px;
            }
            .post-item {
                width: 50%;
                .card-title {
                    font-size: 12px;
                    height: inherit;
                    overflow: inherit;
                }
            }
            .detail {
                .detail-header {
                    h1 {
                        font-size: 18px;
                        padding-bottom: 20px;
                    }
                }
                .detail-content {
                    width: 100%;
                    padding-right: 0;
                }
            }
            .comment-box {
                margin-right: 0;
            }
        }
    }
}

@media only screen and (min-width: 600px) {
    /* For tablets: */
    .col-s-1 {
        width: 8.33%;
    }
    .col-s-2 {
        width: 16.66%;
    }
    .col-s-3 {
        width: 25%;
    }
    .col-s-4 {
        width: 33.33%;
    }
    .col-s-5 {
        width: 41.66%;
    }
    .col-s-6 {
        width: 50%;
    }
    .col-s-7 {
        width: 58.33%;
    }
    .col-s-8 {
        width: 66.66%;
    }
    .col-s-9 {
        width: 75%;
    }
    .col-s-10 {
        width: 83.33%;
    }
    .col-s-11 {
        width: 91.66%;
    }
    .col-s-12 {
        width: 100%;
    }
    .detail .detail-header h1 {
        line-height: 32px;
    }
}
@media only screen and (max-width: 1024px) {
    /* For tablet and mobile: */
    .header-pc {
        display: none;
    }
    .header-mobile {
        display: block;
    }
}
@media only screen and (min-width: 1025px) {
    /* For desktop: */
    .header-pc {
        display: block;
    }
    .header-mobile {
        display: none;
    }
    .col-1 {
        width: 8.33%;
    }
    .col-2 {
        width: 16.66%;
    }
    .col-3 {
        width: 25%;
    }
    .col-4 {
        width: 33.33%;
    }
    .col-5 {
        width: 41.66%;
    }
    .col-6 {
        width: 50%;
    }
    .col-7 {
        width: 58.33%;
    }
    .col-8 {
        width: 66.66%;
    }
    .col-9 {
        width: 75%;
    }
    .col-10 {
        width: 83.33%;
    }
    .col-11 {
        width: 91.66%;
    }
    .col-12 {
        width: 100%;
    }
}

@media only screen and (min-width: 1600px) {
    /* For large desktop: */
    .col-l-1 {
        width: 8.33%;
    }
    .col-l-2 {
        width: 16.66%;
    }
    .col-l-3 {
        width: 25%;
    }
    .col-l-4 {
        width: 33.33%;
    }
    .col-l-5 {
        width: 41.66%;
    }
    .col-l-6 {
        width: 50%;
    }
    .col-l-7 {
        width: 58.33%;
    }
    .col-l-8 {
        width: 66.66%;
    }
    .col-l-9 {
        width: 75%;
    }
    .col-l-10 {
        width: 83.33%;
    }
    .col-l-11 {
        width: 91.66%;
    }
    .col-l-12 {
        width: 100%;
    }
}

@media only screen and (max-width: 1100px) {
    .main-content {
        .container {
            border-radius: 0;
            -webkit-border-radius: 0;
            -moz-border-radius: 0;
            -ms-border-radius: 0;
            -o-border-radius: 0;
        }
    }
    .container {
        width: 100%;
    }
    .home {
        .promotion {
            .left-promotion {
                width: 75%;
                box-sizing: border-box;
                img {
                    width: 100%;
                }
            }
            .right-promotion {
                width: 25%;
                box-sizing: border-box;
                img {
                    width: 100%;
                }
            }
        }
    }
    .main-ct-left {
        width: 75%;
    }
    .main-ct-right {
        width: 25%;
    }
    header {
        .top-menu {
            padding-right: 10px;
        }
        .header {
            .logo {
                padding-left: 10px;
            }
            .main-menu {
                padding-right: 10px;
            }
        }
    }
}
@media only screen and (max-width: 800px) {
    .home {
        .promotion {
            .left-promotion {
                width: 100%;
            }
            .right-promotion {
                width: 100%;
                padding-left: 0;
                a {
                    width: 50%;
                    float: left;
                    padding: 10px;
                    box-sizing: border-box;
                    img {
                        width: 100%;
                    }
                    &:last-child {
                        margin-top: 0;
                    }
                }
            }
        }
    }
    .main-ct-left {
        width: 100%;
    }
    .main-ct-right {
        width: 100%;
    }
    .list-box {
        flex-direction: column;
    }
    .right-box {
        .post-item {
            width: 50%;
        }
    }
}

@media only screen and (max-width: 650px) {
    .main-content {
        .container {
            margin: 0;
        }
        .detail {
            padding: 0;
        }
    }
    header {
        .top-menu {
            ul {
                display: none;
            }
        }
        .header {
            padding: 0;
            .logo {
                padding: 10px;
                img {
                    width: 100px;
                }
            }
            .main-menu {
                width: 100%;
                background: #059460;
                padding: 20px;
            }
        }
    }
}

@media only screen and (max-width: 450px) {
    header {
        .header {
            .main-menu {
                width: 100%;
                background: #059460;
                padding: 10px;
                ul {
                    li {
                        padding-right: 20px;
                        padding-left: 0;
                        a {
                            font-size: 14px;
                        }
                    }
                }
            }
        }
    }
    .main-container {
        .right-box {
            div {
                .post-item {
                    width: 100%;
                }
            }
        }
    }
}

@media only screen and (max-width: 350px) {
    .main-container {
        .main-content {
            .post-item {
                width: 100%;
            }
        }
    }
}
