header {
    background: #27ae60 url('../assets/images/header-bg.svg') no-repeat center
        center;
    width: 100%;
    float: left;
    .top-menu {
        background: rgba(0, 0, 0, 0.3);
        width: 100%;
        padding: 5px 0;
        float: left;
        box-sizing: border-box;
        .search {
            background: #c9fbdd;
            height: 20px;
            font-size: 14px;
            padding: 4px;
            border: none;
            width: 230px;
            float: right;
            display: flex;
            i {
                color: #007048;
            }
            input {
                background: #c9fbdd;
                border: none;
                font-size: 14px;
                padding-left: 8px;
                outline: none;
                width: 200px;
            }
        }
        ul {
            float: right;
            li {
                float: left;
                padding: 6px 0 6px 30px;
                a {
                    font-weight: bold;
                    font-size: 14px;
                    line-height: normal;
                    color: #9df5c0;
                    display: flex;
                    align-items: center;
                    text-decoration: none;
                    img {
                        margin-right: 5px;
                    }
                    &.actived {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
    .header {
        width: 100%;
        float: left;
        padding-bottom: 30px;
        .logo {
            padding: 20px 0;
            float: left;
        }
        .main-menu {
            float: right;
            padding-top: 40px;
            box-sizing: border-box;
            ul {
                li {
                    float: left;
                    padding-left: 30px;
                    &:first-child {
                        display: none;
                    }
                    a {
                        font-weight: bold;
                        font-size: 16px;
                        line-height: normal;
                        color: #9df5c0;
                        text-transform: uppercase;
                        text-decoration: none;
                        &.actived {
                            color: #ffffff;
                        }
                    }
                }
            }
        }
    }
}
